#loader {
  width: 50px;
  height: 50px;
  top: 50%;
  left: 50%;
  margin-top: -25px;
  margin-left: -25px;
  -webkit-animation: rotate-loader 2200ms infinite linear;
          animation: rotate-loader 2200ms infinite linear;
}
@-webkit-keyframes rotate-loader {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes rotate-loader {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
#loader img#solar1 {
  height: 100%;
  width: 100%;
}



